<template>
  <div class="bac">
    <div class="login">
      <!-- 头部区域 -->
      <div class="loginHeader">
        <img src="../assets/touxiang.png" />
        <h2>七星付费后台管理系统</h2>
        <p>专业数字化管理与用户运营工具，提高知识付费转化率</p>
        <p class="p_2">账号密码登录</p>
      </div>
      <!-- 登录框 -->
      <el-form ref="loginForm" :rules="rules" :model="ruleForm">
        <!-- 账号 -->
        <el-form-item prop="username">
          <el-input class="inputText" v-model="ruleForm.username" prefix-icon="el-icon-user-solid" placeholder="账号">
          </el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="pwd">
          <el-input v-model="ruleForm.pwd" class="inputText" prefix-icon="el-icon-lock" placeholder="密码"
            @keyup.enter.native="submitLogin" show-password></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <div class="loginFooter">
        <el-checkbox>自动登录</el-checkbox>
        <el-link type="primary">忘记密码？</el-link>
      </div>
      <el-button type="primary" class="tijiao" @click="submitLogin">提交</el-button>

      <!-- 微信登录 -->
      <div class="loginFooter2">
        <span>微信登录</span>
        <div>
          <img :src="ipc" alt="图片丢失" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      ruleForm: {
        username: "",
        pwd: ""
      },

      // 表单校验
      rules: {
        username: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 10 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 16, message: "长度在 3 到 16 个字符", trigger: "blur" }
        ]
      },
      ipc: "https://cdn3.axureshop.com/demo/1903216/images/%E7%99%BB%E5%BD%95/u323.svg"
    };
  },
   mounted () {
    this.$notify({
          title: '默认账号与密码为',
          message: ' admin   123456',
          type: 'success',
          duration:5500
        });
   },
  methods: {
    submitLogin() {
      // 获取表单对象
      this.$refs['loginForm'].validate((valid) => {
        // console.log("查看valid", valid);
        if (valid) {
          if (this.ruleForm.username === 'admin' && this.ruleForm.pwd === "123456") {
            this.$message({ showClose: true, message: '登陆成功', type: 'success' });
            // vuex
            this.$store.commit('SetUserName', this.ruleForm.username)
            this.$store.commit('SetPassword', this.ruleForm.pwd)
            // 跳
            this.$router.push({
              path: '/home',
              query: { username: this.ruleForm.username }
            })
          } else {
            this.$message({ showClose: true, message: "登陆失败", type: 'error' });
          }

        }
      })




    }
  }
};
</script>

<style lang="scss" scoped>
.bac {
  display: flex;
  height: 100%;
  background: url("@/assets/bsc.png") no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.loginHeader {
  width: 100%;
  text-align: center;

  p {
    color: gray;
    font-size: 16px;
  }

  img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-top: -50px;
  }

  .p_2 {
    color: #1890FF;
  }
}

.login {
  width: 501px;
  height: 497px;
  border-radius: 5%;
  transform: rotateX(-25%);
  top: 25%;
  margin-left: 10%;
  position: absolute;
  /* background-color: pink; */
  box-shadow: 0 0px 5px 5px #ddd;
}

/* 底部区域1 */
.loginFooter {
  width: 100%;
  margin-top: 10px;
  margin: 5px;
  display: flex;
  justify-content: space-between;
}

.tijiao {
  // position: absolute;
  margin-top: 20px;
  width: 100%;
}

.inputText {
  font-size: 18px !important;
}

// 最底部 微信登录
.loginFooter2 {
  display: flex;
  margin-top: 25px;
  width: 100%;
  align-items: center;

  span {
    padding: 0 8px;
  }

  div {
    background-color: #d8dadd;
    display: flex;
    align-items: center;
    height: 50px;
    width: 50px;
    justify-content: center;
    border-radius: 50%;

    img {
      width: 35px;
      height: 35px;

    }

  }

}
</style>